@import "fonts";
@import "vars";
@import "global";

/*---------------header---------------------*/
.header {
	// .header__container
	min-height: 72px;
	padding: 20px 0px;
	background-color: #0d0d0d;

	&__container {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	// .header__logo

	&__logo {
		@media (max-width: (415px)) {
			max-width: 30%;
		}
	}

	// .header__nav

	&__nav {
		ul {
			display: flex;
			align-items: center;
			justify-content: center;
			@media (max-width: ($md2 + px)) {
				flex-direction: column;
				align-items: flex-start;
			}
		}
		li {
			margin: 0 11 / $maxWidthContainer * 100vw 0 0px;
			@media (max-width: ($md2 + px)) {
				text-align: left;
				&:not(:last-child) {
					margin: 0px 0px 20px 0px;
				}
			}
		}
		a {
			color: #fff;
			&:hover {
				color: #1b7ff3;
				text-decoration: underline;
			}
			@media (max-width: ($md2 + px)) {
				font-size: 22px;
			}
		}
	}

	// .header__callback

	&__callback {
	}
}
.callback-header {
	// .callback-header__phone
	display: flex;
	align-items: center;
	@media (max-width: ($md1 + px)) {
		flex-direction: column;
	}
	@media (max-width: ($md2 + px)) {
		flex-direction: row;
	}
	@media (max-width: (575px)) {
		flex-direction: column;
	}
	&__phone {
		color: #f2f2f2;
		font-weight: bold;
		font-family: Roboto;
		margin: 0 17 / $maxWidthContainer * 100vw 0 0px;
		border: 1px solid transparent;
		margin-bottom: -3px;
		padding-bottom: 3px;
		&:hover {
			border-bottom: 1px solid #fff;
		}
		@media (max-width: ($md1 + px)) {
			margin: 0px 0px 10px 0px;
		}
		@media (max-width: ($md2 + px)) {
			margin: 0 15 / $maxWidthContainer * 100vw 0 0px;
		}
		@media (max-width: (575px)) {
			margin: 0px 0px 10px 0px;
		}
		@media (max-width: ($md4 + px)) {
			font-size: 22px;
			margin-top: 30px;
			display: block;
		}
	}

	// .callback-header__button

	&__button {
		font-size: 18px;
		color: #fff;
		display: flex;
		align-items: center;
		justify-content: center;
		border: 1px solid #1f4f96;
		border-radius: 6px;
		padding: 10.5px 22px;
		&:hover {
			background-color: #1f4f96;
		}
	}
}
/*---------------header---------------------*/
/*---------------main-section---------------------*/
.main-section {
	// .main-section__container
	padding: 45px 0px 98px 0px;
	position: relative;
	@media (max-width: ($md4 + px)) {
		padding-bottom: 50px;
		border: 1px solid rgba(209, 209, 209, 0.3);
	}
	&__container {
		&::after {
			content: "";
			position: absolute;
			height: 1px;
			width: 98%;
			background-color: rgba(209, 209, 209, 0.3);
			bottom: -98px;
			@media (max-width: ($md4 + px)) {
				display: none;
			}
		}
	}

	// .main-section__slider

	&__slider {
		.swiper-pagination {
			left: 70px;
			right: auto;
			text-align: left;
			top: calc(100% - 82px);
			@media (max-width: ($md4 + px)) {
				left: 22px;
				top: calc(100% - 52px);
			}
		}
	}
}
.main-slider {
	// .main-slider__item

	&__item {
		padding: 102px 0px 107px 58px;
		border-radius: 20px;
		background-repeat: no-repeat;
		background-size: cover;
		@media (max-width: (575px)) {
			padding: 102px 0px 107px 30px;
		}
		@media (max-width: ($md4 + px)) {
			padding: 30px 15px 70px 15px;
		}
	}

	// .main-slider__title

	&__title {
		font-size: 48px;
		font-weight: bold;
		max-width: 464px;
		text-align: left;
		@media (max-width: (575px)) {
			font-size: 34px;
		}
	}

	// .main-slider__subtitle

	&__subtitle {
		max-width: 519px;
		text-align: left;
		margin: 16px 0px 20px 0px;
	}

	// .main-slider__button

	&__button {
	}

	// .main-slider__answer

	&__answer {
		margin-top: 11px;
		color: #565656;
	}
}
/*---------------main-section---------------------*/
/*---------------sell-section---------------------*/
.sell-section {
	// .sell-section__container
	padding: 40px 0px 108px 0px;
	position: relative;

	@media (max-width: ($md2 + px)) {
		.visible-hand {
			display: flex;
		}
	}
	@media (max-width: ($md3 + px)) {
		padding-bottom: 50px;
		border-bottom: 1px solid rgba(209, 209, 209, 0.3);
	}

	&_items {
		padding-top: 114px;
		@media (max-width: ($md3 + px)) {
			padding-top: 50px;
		}
	}
	&__container {
		&::after {
			content: "";
			position: absolute;
			height: 1px;
			width: 98%;
			background-color: rgba(209, 209, 209, 0.3);
			bottom: -98px;
			@media (max-width: ($md3 + px)) {
				display: none;
			}
		}
		@media (max-width: ($md2 + px)) {
			max-width: 100%;
			padding-right: 0;
		}
	}

	// .sell-section__title

	&__title {
		@media (max-width: ($md2 + px)) {
			padding-right: 15px;
		}
	}

	// .sell-section__subtitle

	&__subtitle {
		max-width: 500px;
	}

	&__items {
	}
}
.sell-items {
	// .sell-items__column
	margin-top: 40px;
	display: grid;
	grid-template-columns: 1fr 1fr 1fr 1fr;
	gap: 21px;
	@media (max-width: ($md1 + px)) {
		grid-template-columns: 1fr 1fr 1fr;
	}
	@media (max-width: ($md2 + px)) {
		display: flex;
		align-items: flex-start;
		flex-wrap: nowrap;
		overflow-x: scroll;
		margin-top: 10px;
	}

	&__column {
		min-width: 285px;
		position: relative;
		background-color: #313131;
		border: 1px solid #3c3c3c;
		padding: 45px 30px 40px 30px;
		border-radius: 16px;
		cursor: pointer;
		color: #fff;
		height: 100%;
		display: flex;
		flex-direction: column;
		&:hover {
			.sell-items__img {
				transition: all 0.4s;
				filter: grayscale(0);
			}
		}
		@media (max-width: ($md2 + px)) {
			height: 480px;
		}
	}

	&__block {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: flex-start;
		flex: 1 1 auto;
	}

	// .sell-items__warehouse

	&__warehouse {
		position: absolute;
		top: 15px;
		right: 17px;
		padding: 10px;
		background-color: rgb(#275daf, 0.3);
		border: 1px solid #275daf;
		border-radius: 7px;
		color: #fff;
	}

	// .sell-items__img

	&__img {
		transition: all 0.4s;
		flex: 0 0 172px;
		display: flex;
		align-items: center;
		margin-bottom: 20px;
		filter: grayscale(1);
	}

	// .sell-items__text

	&__text {
		display: flex;
		flex-direction: column;
		height: 100%;
		flex: 1 1 auto;
	}

	// .sell-items__title

	&__title {
		font-weight: bold;
		color: #fff;
	}

	// .sell-items__subtitle

	&__subtitle {
		font-size: 14px;
		color: #717171;
		line-height: calc(17 / 14 * 100%);
	}

	// .sell-items__button

	&__button {
	}

	&__more {
		display: block;
		margin-top: 13px;
		font-size: 14px;
		text-align: left;
		margin-left: 0;
		width: 100%;
		color: #fff;
		&:hover {
			color: #1b7ff3;
			span {
				&::after {
					background-color: #1b7ff3;
				}
			}
		}
		span {
			position: relative;
			&::after {
				transition: all 0.4s;
				position: absolute;
				content: "";
				height: 1px;
				background-color: #fff;
				width: 100%;
				bottom: 0;
				left: 0;
			}
		}
	}
	&__about {
		display: flex;
		flex-direction: column;
		width: 100%;
	}
}

