.accordion-item {
	background-color: transparent;
	border: none;
	&:not(:last-child) {
		margin-bottom: 16px;
	}
}

.accordion-button {
	height: 78px;
	border-radius: 16px !important;
	background-color: #313131;
	border: 1px solid #3c3c3c !important;
	color: #bab6b6;
	font-size: 20px;
	position: relative;
	&:not(.collapsed) {
		background-color: #313131;
		color: #bab6b6;
		&::after {
			background-image: url("../img/arrow.svg");
			transform: rotate(-180deg);
		}
	}
	&:focus {
		box-shadow: 0 0 0 0.25rem rgb(#bab6b6, 25%);
	}
	&::after {
		background-image: url("../img/arrow.svg");
	}
}
.warning {
	position: absolute;
	right: 90px;
	background-color: rgb(#dd1236, 25%);
	border: 1px solid #dd1236;
	border-radius: 7px;
	padding: 10px;
	@media (max-width: ($md4 + px)) {
		top: 0;
		left: 0;
		border-radius: 16px;
		padding: 5px;
		font-size: 12px;
		display: inline-table;
	}
}
/*------------footer-------------*/
.footer {
	// .footer__container
	&__container {
	}

	// .footer__top

	&__top {
		border-top: 1px solid rgb(#d1d1d1, 30%);
		display: flex;
		justify-content: space-between;
		padding: 30px 0px 30px 0px;
		border-bottom: 1px dashed rgb(#d1d1d1, 30%);
		@media (max-width: ($md2 + px)) {
			flex-direction: column;
		}
	}
	&__bottom {
		padding: 30px 0px 30px 0px;
		border-bottom: 1px dashed rgb(#d1d1d1, 30%);
	}
	&__text {
		padding: 22px 0px 30px 0px;
		p {
			font-size: 18px;
			line-height: calc(22 / 18 * 100%);
			color: #726e6e;
			&:not(:last-child) {
				margin-bottom: 28px;
			}
		}
	}
}
.footer-title {
	font-size: 20px;
	font-weight: bold;
}
.footer-href {
	// .footer-href__column

	&__column {
		display: block;
		&_sert {
			column-count: 3 !important;
			column-gap: 50px;
			@media (max-width: ($md2 + px)) {
				column-count: 2 !important;
				column-gap: 20px;
			}
			@media (max-width: (650px)) {
				column-count: 1 !important;
			}
		}
	}
	&__item {
		@media (max-width: ($md2 + px)) {
			order: 2;
			&:not(:last-child) {
				margin: 0px 0px 20px 0px;
			}
		}
		&:nth-child(1) {
			.footer-href__column {
				column-count: 2;
				column-gap: 33px;
				@media (max-width: ($md4 + px)) {
					column-count: 1;
				}
			}
		}
	}
	// .footer-href__title

	&__title {
		margin-bottom: 25px;
	}

	// .footer-href__nav

	&__nav {
	}

	// .footer-href__list

	&__list {
		&:not(:last-child) {
			margin-bottom: 16px;
		}
		&_sert {
			&:not(:last-child) {
				margin-bottom: 13px;
			}
		}
	}

	// .footer-href__source
	&__soc {
		display: flex;
		flex-direction: column;
		align-items: flex-end;
	}
	&__source {
		font-size: 18px;
		color: #bcbcbc;
		line-height: calc(22 / 18 * 100%);
		&_sert {
			color: #fff;
			position: relative;
			padding-left: 42px;
			&::before {
				position: absolute;
				content: "";
				left: 0;
				top: 0;
				bottom: 0;
				margin: auto;
				background-image: url("../img/document.svg");
				background-repeat: no-repeat;
				background-position: center;
				width: 24px;
			}
		}
		&:hover {
			text-decoration: underline;
		}
		@media (max-width: ($md1 + px)) {
			font-size: 16px;
		}
	}
}

.footer-soc {
	// .footer-soc__logo
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	@media (max-width: ($md2 + px)) {
		order: 1;
		flex-direction: row;
		align-items: flex-start;
		justify-content: space-between;
		margin: 0px 0px 30px 0px;
	}
	&__logo {
		margin-bottom: 25px;
	}

	// .footer-soc__phone

	&__phone {
		display: flex;
		flex-direction: column;
		align-items: flex-end;
	}

	// .footer-soc__href

	&__href {
		font-size: 26px;
		color: #fff;
		transition: all 0.4s;
		border-bottom: 1px solid transparent;
		&:hover {
			border-bottom: 1px solid #fff;
		}
		@media (max-width: ($md1 + px)) {
			font-size: 20px;
		}
	}

	// .footer-soc__text

	&__text {
		margin-top: 17px;
		span {
			color: #285fb2;
		}
	}

	// .footer-soc__social

	&__social {
		margin-top: 18px;
	}

	// .footer-soc__icon

	&__icon {
		transition: all 0.4s;
		&:not(:last-child) {
			margin-right: 14px;
		}

		&:hover {
			transition: all 0.4s;
			path {
				transition: all 0.4s;
				fill: #285fb2;
			}
		}
	}
}
.contact-block {
	padding-bottom: 120px;
	&__item {
		margin: 0;
		padding-top: 40px;
		&:not(:last-child) {
			padding-bottom: 50px;
			border-bottom: 1px solid rgb(#d1d1d1, 30%);
		}
	}
}

.goods {
	@media (max-width: ($md3 + px)) {
		.visible-hand {
			display: flex;
		}
	}
	@media (max-width: ($md2 + px)) {
		.goods__cur .visible-hand {
			display: flex;
		}
	}
	&__block {
		padding: 30px 0px 90px 0px;
		border-bottom: 1px solid rgba(209, 209, 209, 0.3);
		@media (max-width: ($md4 + px)) {
			padding-bottom: 50px;
		}
	}
	// .goods__dicription

	&__discription {
		padding: 50px 0px 60px 0px;
		@media (max-width: ($md4 + px)) {
			padding-bottom: 30px;
		}
	}
	&__other {
		padding: 24px 0px 65px 0px;
		@media (max-width: ($md4 + px)) {
			padding-bottom: 50px;
		}
	}
	&__title {
		padding-bottom: 55px;
		@media (max-width: ($md3 + px)) {
			padding-bottom: 20px;
		}
	}
}
.btn-close {
	background-image: url("../img/close.svg");
	top: 32px;
	right: 32px;
	position: absolute;
	z-index: 99;
	@media (max-width: ($md4 + px)) {
		top: 5px;
		right: 5px;
	}
}
.callback-form {
