.swiper {
	width: 100%;
	height: 100%;
}
svg,
path {
	transition: all 0.4s;
}
.swiper-slide {
	text-align: center;

	/* Center slide text vertically */
	display: -webkit-box;
	display: -ms-flexbox;
	display: -webkit-flex;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: flex-start;
}

.swiper-slide img {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;
}
.swiper-slider {
	height: 100%;
	box-sizing: border-box;
	width: 100%;
	overflow: hidden;
	margin: 0px auto 0px auto;
}
.swiper-pagination-bullet {
	background-color: #40464d;
	min-width: 14px;
	min-height: 14px;
	opacity: 1;
}
.swiper-pagination-bullet-active {
	background-color: #1f4f96;
}
.section-title {
	font-size: 28px;
	font-weight: bold;
	color: #fff;
	line-height: calc(34 / 28 * 100%);
}
.section-subtitle {
	margin-top: 20px;
	color: #dbdbdb;
	line-height: calc(19 / 16 * 100%);
}
.main-btn {
	color: #fff;
	background-color: #1f4f96;
	font-size: 18px;
	min-width: 247px;
	min-height: 53px;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 6px;
	cursor: pointer;
	transition: all 0.4s;
	&:hover {
		background-color: rgb(#1f4f96, 0.8);
	}
	@media (max-width: ($md4 + px)) {
		min-width: 100%;
	}
}
.main-btn_small {
	width: 245px;
}
.main-btn_100 {
	min-width: 100%;
	background-color: transparent;
	border: 1px solid #1f4f96;
	&:hover {
		background-color: rgb(#1f4f96, 0.8);
		border: transparent;
	}
}
.border {
	background-color: #313131;
	border: #3c3c3c;
	border-radius: 16px;
}
.breadcrumbs {
	// .breadcrumbs__container
	padding: 36px 0px 20px 0px;
	color: #275daf;
	a {
		color: #717171;
	}
	&__container {
	}
}

/*-----------item-part------------*/
.product-title {
	font-size: 26px;
	font-weight: bold;
	line-height: calc(32 / 26 * 100%);
}
.product-subtitle {
	font-size: 16px;
	color: #717171;
	line-height: calc(19 / 16 * 100%);
}
.product {
	// .product__left
	display: flex;
	justify-content: space-between;
	align-items: flex-end;
	@media (max-width: ($md2 + px)) {
		flex-direction: column-reverse;
		align-items: flex-start;
	}
	&__left {
		max-width: 601px;
	}

	// .product__img

	&__img {
		@media (min-width: 992px) {
			min-width: 493px;
			height: 100%;
		}
		@media (max-width: ($md2 + px)) {
			margin-bottom: 30px;
		}
		border-radius: 20px;
		overflow: hidden;
		margin: 0 auto;
		img {
			object-fit: cover;
			width: 100%;
		}
		&_small {
			img {
				width: auto;
			}
		}
	}
}
.product-block {
	// .product-block__title

	&__title {
	}

	// .product-block__discription

	&__discription {
		margin-top: 20px;
		margin-bottom: 30px;
	}

	// .product-block__download

	&__download {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 303px;
		height: 47px;
		background-color: rgba(196, 196, 196, 0.1);
		border-radius: 3px;
		color: #fff;
		svg {
			margin-right: 14px;
		}
		&:hover {
			color: #275daf;
			transition: all 0.4s;
			svg {
				transition: all 0.4s;
				path {
					transition: all 0.4s;
					fill: #275daf;
				}
			}
		}
		@media (max-width: ($md4 + px)) {
			width: 100%;
		}
	}

	// .product-block__price

	&__price {
		margin-top: 29px;
	}
}

.price-product {
	// .price-product__title

	&__title {
		margin-bottom: 9px;
	}

	// .price-product__subtitle

	&__subtitle {
		margin-bottom: 30px;
	}

	// .price-product__button

	&__button {
		@media (max-width: ($md4 + px)) {
			max-width: 100%;
		}
	}
}
.discription-block {
	// .dicription-block__title

	&__title {
		margin-bottom: 30px;
	}
	&__subtitle {
		max-width: 948px;
	}
}
.other-block {
	// .other-block__items

	&__items {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		@media (max-width: ($md3 + px)) {
			overflow-x: scroll;
		}
	}

	&__column {
		@media (max-width: ($md1 + px)) {
			max-width: 32%;
		}
		@media (max-width: ($md3 + px)) {
			min-width: 280px;
			margin-right: 15px;
		}
		img {
			border-radius: 16px;
		}
	}
}
.offcanvas-btn {
	display: none;
	@media (max-width: ($md2 + px)) {
		display: block;
	}
}
.offcanvas {
	background-color: #1e1e1e;
}
.visible-hand {
	display: none;
	align-items: center;
	justify-content: center;
	margin: 20px 0px 0px 0px;
}
.fade {
	background-color: rgb(#fff, 0.1);
}
#map {
	height: 522px;
	filter: invert(1) hue-rotate(-190deg);
}
