@font-face {
	font-family: "Verdana";
	src: url("../fonts/Verdana-Bold.eot");
	src: url("../fonts/Verdana-Bold.eot?#iefix") format("embedded-opentype"),
		url("../fonts/Verdana-Bold.woff2") format("woff2"), url("../fonts/Verdana-Bold.woff") format("woff"),
		url("../fonts/Verdana-Bold.ttf") format("truetype"), url("../fonts/Verdana-Bold.svg#Verdana-Bold") format("svg");
	font-weight: bold;
	font-style: normal;
	font-display: swap;
}

